import { createCustomElement, actionTypes } from "@servicenow/ui-core";
const { COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import "@servicenow/now-tabs";
import parseInt from "lodash/parseInt";
import isFinite from "lodash/isFinite";
import isEqual from "lodash/isEqual";
import isEmpty from "lodash/isEmpty";
import get from "lodash/get";
import { uxfViewportBehavior } from '@devsnc/library-uxf';
import styles from './now-uxf-tab-set.scss';
import {
	getFirstDynamicTabWithRoute,
	getTabData, getSelectedTab,
	hasDynamicTabs,
	isDynamic,
	isIndexInArray
} from "./utils";
import {
	DYNAMIC_PREFIX,
	SELECTED_ITEM_SET,
	SET_SELECTED_TAB,
	UXF_TAB_SET_SELECTION,
	VIEWPORT_ROUTE_REQUESTED
} from './constants';

const view = (state) => {
	const {
		properties: {
			dynamicTabData,
			// Below properties are set by the uxfViewportBehavior
			viewportRoutes,
			activeRoute
		}
	} = state;

	const tabs = getTabData(viewportRoutes, dynamicTabData);
	const selectedItem = getSelectedTab(activeRoute, tabs);

	return (
		<div className="tabset-wrapper">
			<div className="tabset-tabs">
				<now-tabs
					manage-selected-item="true"
					items={tabs}
					selected-item={selectedItem}
					max-width="240"
				/>
			</div>
			<section className="tabset-container">
				<slot name="viewport">
				</slot>
			</section>
		</div>
	);
};

createCustomElement("now-uxf-tab-set", {
	renderer: { type: snabbdom },
	view,
	styles,
	behaviors: [uxfViewportBehavior],
	properties: {
		selectedTabIndex: {
			default: undefined
		},
		defaultRoute: {
			default: ''
		},
		dynamicTabData: {
			default: []
		}
	},
	actionHandlers: {
		[SELECTED_ITEM_SET]: {
			effect({ action, properties, dispatch }) {
				const {
					payload: { value: route }
				} = action;

				const {
					dynamicTabData,
					viewportRoutes
				} = properties;

				const tabs = getTabData(viewportRoutes, dynamicTabData);

				let index;

				if (route.startsWith(DYNAMIC_PREFIX)) {
					const dynamicTabIndex = parseInt(route.substr(DYNAMIC_PREFIX.length));
					if (isNaN(dynamicTabIndex)) {
						console.error(`Provided tab index '${dynamicTabIndex}' is not a valid index`);
						// TODO: Error handling for a bad tab number value. Maybe load last tab in the set? Or first?
						return;
					}

					const dynamicTab = dynamicTabData[dynamicTabIndex];
					index = tabs.findIndex(tab => isEqual(dynamicTab, tab.routeObj));
					dispatch(VIEWPORT_ROUTE_REQUESTED, dynamicTab);
				} else {
					index = tabs.findIndex(tab => tab.id === route);
					dispatch(VIEWPORT_ROUTE_REQUESTED, {
						route: route
					});
				}

				dispatch(UXF_TAB_SET_SELECTION, {
					selectedTabIndex: index
				});
			},
			stopPropagation: true,
		},
		[SET_SELECTED_TAB]: {
			effect({ action, dispatch, properties }) {
				action.stopPropagation();
				const {
					defaultRoute,
					dynamicTabData,
					viewportRoutes,
					selectedTabIndex
				} = properties;

				const tabs = getTabData(viewportRoutes, dynamicTabData);
				const tabIndex = parseInt(selectedTabIndex);

				// Selected tab index inputted
				if (isFinite(parseInt(tabIndex))) {
					if (!isIndexInArray(tabIndex, tabs)) {
						console.error(`Provided selected tab index '${tabIndex}' is not a valid index`);
						// Continue to default route
					}
					else {
						dispatch(SELECTED_ITEM_SET, {
							value: tabs[tabIndex].id
						});
						return;
					}
				}

				if (!viewportRoutes[defaultRoute]) {
					dispatch(SELECTED_ITEM_SET, {
						value: tabs[0].id
					});
					return;
				}

				if (!isDynamic(viewportRoutes[defaultRoute][0])) {
					// Using a satic default route
					dispatch(SELECTED_ITEM_SET, {
						value: defaultRoute
					});
					return;
				}

				// Using a dynamic default route
				const dynamicTab = getFirstDynamicTabWithRoute(tabs, defaultRoute);
				dispatch(SELECTED_ITEM_SET, {
					value: get(dynamicTab, 'id')
				});
			},
			stopPropagation: true
		},
		[COMPONENT_CONNECTED]: ({ dispatch, properties }) => {
			const {
				dynamicTabData,
				viewportRoutes,
			} = properties;

			// if it loads and dynamic tabs aren't ready yet
			if (hasDynamicTabs(viewportRoutes) && !dynamicTabData.length) {
				return;
			}

			if (isEmpty(viewportRoutes)) {
				return;
			}

			dispatch(SET_SELECTED_TAB);
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ dispatch, action, properties }) => {
			const {
				payload: {
					name,
					value,
					previousValue
				}
			} = action;
			switch (name) {
				case 'dynamicTabData': {
					if (!previousValue.length && value.length) {
						dispatch(SET_SELECTED_TAB);
					} else if (value.length) {
						const {
							viewportRoutes,
							activeRoute
						} = properties;
						
						const tabs = getTabData(viewportRoutes, previousValue);
						const selectedItem = getSelectedTab(activeRoute, tabs);

						if(selectedItem != -1) {
							dispatch(SELECTED_ITEM_SET, {
								value: selectedItem
							});
						}
					}
					break;
				}
				case 'selectedTabIndex': {
					dispatch(SET_SELECTED_TAB);
					break;
				}
				case 'viewportRoutes': {
					if (isEmpty(previousValue) && !isEmpty(value)) {
						dispatch(SET_SELECTED_TAB);
					}	
					break;
				}
				default:
					break;
			}

		}
	},
	dispatches: {
		/**
		 * Fired when selecting a tab. Contains the index of the selected tab.
		 * @type {{selectedTabIndex: number}}
		 */
		'UXF_TAB_SET#TAB_SELECTED': {}
	}
});
