import keys from "lodash/keys";
import isEqual from "lodash/isEqual";
import get from "lodash/get";
import omit from "lodash/omit";
import { DYNAMIC_PREFIX } from "./constants";

const mapDynamicTabData = (viewportRoutes, dynamicTabData) =>
dynamicTabData.flatMap((dynamicTabItem, index) => {
	try {
		const requestedRouteObject = viewportRoutes[dynamicTabItem.route][0];
		// TODO: Figure out which requested route object to use based on provided routes -
		// route name
			return [
				{
					id: `${DYNAMIC_PREFIX}${index}`,
					label: dynamicTabItem.label || requestedRouteObject.name,
					routeObj: dynamicTabItem,
					count: dynamicTabItem.count,
					icon: get(requestedRouteObject, "icon", null),
				},
			];
		} catch (e) {
			console.warn(
				"now-uxf-tab-set - invalid dynamic route object. Removing object from tab list.",
				dynamicTabItem
			);
			return [];
		}
	});

/**
 * Map dynamicTabs by order, i.e.:
 * {
 * 	'200': [
 * 		{
 * 			id: 'dynamic_0',
 * 			label: 'Task SLAs - 0'
 * 		},
 * 		{
 * 			id: 'dynamic_1',
 * 			label: 'Affected CIs - 0'
 * 		}
 * 	],
 * 	'300': [
 *  	{
 * 			id: 'dynamic_3',
 * 			label: 'Blah Blah'
 * 		}
 * 	]
 * }
 */
const mapDynamicTabsByOrder = (viewportRoutes, dynamicTabs) => {
	const dynamicTabMap = {};

	dynamicTabs.forEach((dynamicTab) => {
		const route = dynamicTab.routeObj.route;
		const ordering = viewportRoutes[route][0].order;

		if (!dynamicTabMap[ordering]) {
			dynamicTabMap[ordering] = [dynamicTab];
		} else {
			dynamicTabMap[ordering].push(dynamicTab);
		}
	});

	return dynamicTabMap;
};

const combineTabs = (viewportRoutes, dynamicTabMap) => {
	const tabs = [];
	let dynamicTabMapCopy = { ...dynamicTabMap };

	keys(viewportRoutes)
		.map((routePath) => {
			return {
				routePath,
				route: viewportRoutes[routePath][0],
			};
		})
		.sort((firstEl, secondEl) => {
			return firstEl.route.order - secondEl.route.order;
		})
		.forEach(({ routePath, route }) => {
			if (isDynamic(route)) {
				if (dynamicTabMapCopy[route.order]) {
					tabs.push(...dynamicTabMapCopy[route.order]);
					dynamicTabMapCopy = omit(dynamicTabMapCopy, route.order);
				}
			} else {
				tabs.push({
					id: routePath,
					label: route.name,
					icon: route.icon,
				});
			}
		});

	return tabs;
};

const getTabData = (viewportRoutes, dynamicTabData) => {
	let dynamicTabMap = {};

	if (Array.isArray(dynamicTabData) && dynamicTabData.length) {
		const dynamicTabs = mapDynamicTabData(viewportRoutes, dynamicTabData);
		dynamicTabMap = mapDynamicTabsByOrder(viewportRoutes, dynamicTabs);
	}

	return combineTabs(viewportRoutes, dynamicTabMap);
};

const getSelectedTab = (activeRoute, tabs) => {
	if (!activeRoute) {
		return -1;
	}

	if (keys(activeRoute.fields).length === 0) {
		return activeRoute.routeType;
	}

	const dynamicTab = tabs.find(
		(tab) =>
			isEqual(get(tab, "routeObj.fields"), activeRoute.fields) &&
			get(tab, "routeObj.route") === activeRoute.routeType
	);

	if (!dynamicTab) {
		return -1;
	}

	return dynamicTab.id;
};

const isDynamic = (route) => !!get(route, "fields.length", 0);

const getFirstDynamicTabWithRoute = (tabs, defaultRoute) =>
	tabs.find(({ routeObj }) => get(routeObj, "route") === defaultRoute);

const hasDynamicTabs = (viewportRoutes) =>
	keys(viewportRoutes)
		.map((routePath) => {
			return {
				...viewportRoutes[routePath][0],
			};
		})
		.some((route) => isDynamic(route));

const isIndexInArray = (index, array) => index >= 0 && index < array.length;

export {
	getFirstDynamicTabWithRoute,
	getTabData,
	getSelectedTab,
	isDynamic,
	isIndexInArray,
	hasDynamicTabs,
};
